
.site-header {
  padding: 40px;
  font-size: 13px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}
.site-header p {
  line-height: 1.3em;
}
/*
 * Site Header Title
 *
 */
.site-header-title {
  margin-bottom: 30px;
}
.site-header-title h1 {
  font-size: 1.4rem;
  line-height: 120%;
  font-weight: 200;
  margin: 0;
}
.site-header-title h1 span {
  font-size: 2rem;
  line-height: 110%;
  display: block;
}
/*
 * Site Header Nav
 *
 */
.site-header-nav {
  list-style-type: none;
  padding: 0;
  margin: 0 0 30px 0;
}
.site-header-nav__item {
  display: inline-block;
  position: relative;
  margin: 0 5px;
}
.site-header-nav__item a {
  text-decoration: none;
}
.site-header-nav__item span + span {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  width: 0;
  height: 0;
  display: block;
}
/*
 * Site Header List
 *
 */
.site-header-list {
  list-style-type: none;
  padding: 0;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
.site-header-list {
    margin: 50px 0 80px 0;
}
}
.site-header-list__item {
  display: inline-block;
  margin: 0 15px;
}
.site-header-list__item svg,
.site-header-list__item span {
  display: inline-block;
  vertical-align: middle;
}
.site-header-list__item svg {
  width: 14px;
}
.site-header-list__item span {
  padding-left: 8px;
}
.site-header-list__item a {
  text-decoration: none;
}
.site-header-list__item--aspen:hover a {
  color: #FFC709;
}
.site-header-list__item--aspen:hover svg path {
  fill: #FFC709;
}



