
.latest-track {
  display: table;
  border: 1px #dddde4 solid;
  border-radius: 2px;
  line-height: 100%;
  text-align: left;
  text-decoration: none;
  margin: 30px auto 0;
  padding: 5px;
  max-width: 250px;
}
.latest-track__cover img {
  width: 75px;
  height: auto;
}
.latest-track__cover,
.latest-track__info {
  display: table-cell;
  vertical-align: top;
}
.latest-track__info {
  font-size: 11px;
  line-height: 120%;
  padding: 5px 8px;
  color: #787887;
}
.latest-track__name {
  font-weight: bold;
}
.latest-track__name,
.latest-track__artist {
  display: block;
}
.latest-track__date {
  display: block;
  font-size: 9px;
  margin-top: 5px;
}



